




























import Vue from "vue";
import Component from "vue-class-component";
import mixin from "../../config/mixin";
import { Action } from "vuex-class";
import { Toast } from "vant";
import comhead from "../../components/Comhead.vue";

@Component({
  components: { comhead },
  mixins: [mixin]
})
export default class collection extends Vue {
  goodsList = [];
  checkedGoods = [];
  isShow = false;
  classindex=999;
  goGoodsId = "";
  toggle() {
    this.isShow = !this.isShow;
  }
  over(index){
    this.classindex=index
  }
  out(index){
    this.classindex=NaN;    
  }
  goDetails(){
    console.log('开始跳转')
    
  }
  favdelete(item) {
    Vue.prototype.$reqFormPost(
      "/fav/delete",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        goodsIds: item.goodsId
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        this.getProductList();
      }
    );
  }
  allchange() {
    if (this.goodsList.length == this.checkedGoods.length) {
      this.checkedGoods = [];
    } else {
      let result = [];
      this.goodsList.forEach((item, index) => {
        console.log();
        result.push(item.goodsId);
      });
      this.checkedGoods = result;
    }
  }
  getProductList() {
    Vue.prototype.$reqFormPost1(
      "/fav/query",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        page: 0,
        pageSize: 20
      },
      res => {
        if (res.returnCode != 200) {
                  this["$Message"].warning(res.message);
          return;
        }
        console.log("收藏", res.data.goodsList);
        this.goodsList = res.data.goodsList;
      }
    );
  }

  goProductDetail(goodsId) {
    this.$router.push({
      path: "/productdetail",
      query: {
        goodsId: goodsId
      }
    });
  }

  handlePX(CssName, PxNumber) {
    return (
      CssName +
      ":" +
      this.$store.getters[Vue.prototype.MutationTreeType.SYSTEM].availWidth /
        750 *
        PxNumber +
      "px;"
    );
  }

  mounted() {
    this.getProductList();
    console.log("收藏页面");
    this.$emit('selectMenu',{
      name: '我的收藏',
      url:'/collection',
    })
  }
}



